import { useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { getCTokenAddress, getTokenAddress, getYieldTokenAddress } from 'utils/addressHelpers'
import erc20ABI from 'config/abi/erc20.json'
import multicall from 'utils/multicall'
import { useMasterchef } from './useContract'
import useRefresh from './useRefresh'
import { useChainId } from './useTheme'

const useAgentInfo = (): any => {
  const [val, setVal] = useState()
  const { account }: { account: string } = useWallet()
  const { fastRefresh } = useRefresh()
  const masterChefContract = useMasterchef(0)
  useEffect(() => {
    if (account && !val) masterChefContract.methods.members(account).call().then(setVal)
  }, [account, val, masterChefContract, fastRefresh])
  return val
}

const zero = new BigNumber(0)

export const useSupplyState = () => {
  const [balance, setBalance] = useState([zero, zero, zero])
  const sdo = getTokenAddress()
  const sso = getYieldTokenAddress()
  const sbo = getCTokenAddress()
  const { chainId } = useChainId()

  useEffect(() => {
    const fetchTotals = async () => {
      const calls = [
        {
          address: sdo,
          name: 'totalSupply',
          params: [],
        },
        {
          address: sso,
          name: 'totalSupply',
          params: [],
        },
        {
          address: sbo,
          name: 'totalSupply',
          params: [],
        },
      ]

      const [totalSdo, totalSso, totalSbo] = await multicall(chainId, erc20ABI, calls)
      setBalance([
        new BigNumber(totalSdo).div(new BigNumber(10).pow(18)),
        new BigNumber(totalSso).div(new BigNumber(10).pow(18)),
        new BigNumber(totalSbo).div(new BigNumber(10).pow(18)),
      ])
    }

    fetchTotals()
  }, [sdo, sbo, sso, chainId])

  return balance
}

export default useAgentInfo
