import React, { useState } from 'react'
import { ThemeProvider as SCThemeProvider } from 'styled-components'
import { dark, light } from '../theme'

const CACHE_KEY = 'CHAINID'

const ThemeContext = React.createContext({ isDark: null, setIsDark: null })

const ThemeContextProvider = ({ children }) => {
  const [isDark, setIsDark] = useState(() => {
    const x = localStorage.getItem(CACHE_KEY)
    const isDarkUserSetting = parseInt(x ? JSON.parse(x) : process.env.REACT_APP_CHAIN_ID)
    return isDarkUserSetting === 250
  })

  return (
    <ThemeContext.Provider value={{ isDark, setIsDark }}>
      <SCThemeProvider theme={isDark ? dark : light}>{children}</SCThemeProvider>
    </ThemeContext.Provider>
  )
}

export { ThemeContext, ThemeContextProvider }
