import React, { useEffect } from 'react'
import * as bsc from '@binance-chain/bsc-use-wallet'
import { Provider } from 'react-redux'
import getRpcUrl from 'utils/getRpcUrl'
import { LanguageContextProvider } from 'contexts/Localisation/languageContext'
import { ThemeContextProvider } from 'contexts/ThemeContext'
import { BlockContextProvider } from 'contexts/BlockContext'
import { ChainContextProvider } from 'contexts/ChainContext'
import { RefreshContextProvider } from 'contexts/RefreshContext'
import { useChainId } from 'hooks/useTheme'
import store from 'state'
import { ModalProvider } from './uikit/widgets/Modal'

const ProviderSubs: React.FC = ({ children }) => {
  const { chainId, toggleChainId } = useChainId()
  const rpcUrl = getRpcUrl(chainId)

  useEffect(() => {
    (window as any).ethereum.on('networkChanged', (y) => toggleChainId(y))
  }, [toggleChainId])
  return (
    <bsc.UseWalletProvider
      chainId={chainId}
      connectors={{
        walletconnect: { rpcUrl },
        bsc,
      }}
    >
      <BlockContextProvider chainId={chainId}>
        <RefreshContextProvider>
          <ModalProvider>{children}</ModalProvider>
        </RefreshContextProvider>
      </BlockContextProvider>
    </bsc.UseWalletProvider>
  )
}

const Providers: React.FC = ({ children }) => {
  return (
    <Provider store={store}>
      <ChainContextProvider>
        <ThemeContextProvider>
          <LanguageContextProvider>
            <ProviderSubs>{children}</ProviderSubs>
          </LanguageContextProvider>
        </ThemeContextProvider>
      </ChainContextProvider>
    </Provider>
  )
}

export default Providers
