import { useWallet } from '@binance-chain/bsc-use-wallet'
import React, { useMemo } from 'react'
import Button from '../../components/Button/Button'
import { useWalletModal } from '../WalletModal'
import { Login } from '../WalletModal/types'

interface Props {
  account?: string
  login: Login
  logout: () => void
  info: any
}

const UserBlock: React.FC<Props> = ({ account, login, logout, info = null }) => {
  const { onPresentConnectModal, onPresentAccountModal } = useWalletModal(login, logout, account, info?.invitationsLeft)
  const accountEllipsis = account ? `${account.substring(0, 4)}...${account.substring(account.length - 4)}` : null
  const { chainId } = useWallet()
  const label = useMemo(() => (parseInt(chainId.toString()) === 250 ? 'FANTOM' : 'BSC'), [chainId])

  return (
    <div>
      <Button
        size="sm"
        onClick={() => {
          console.info(chainId)
        }}
      >
        {label}
      </Button>
      {account ? (
        <Button
          size="sm"
          variant="tertiary"
          onClick={() => {
            if (label === 'BSC') onPresentAccountModal()
          }}
        >
          {label === 'BSC' ? `${accountEllipsis} - Invitations: ${info?.invitationsLeft || 0}` : accountEllipsis}
        </Button>
      ) : (
        <Button
          size="sm"
          onClick={() => {
            onPresentConnectModal()
          }}
        >
          Connect
        </Button>
      )}
    </div>
  )
}

export default UserBlock
